<template>
  <v-app>
    <v-container
      id="login"
      class="fill-height justify-center"
      tag="section"
    >
      <v-row justify="center">
        <v-slide-y-transition appear>
          <base-material-card
            color="success"
            light
            max-width="100%"
            width="400"
            class="px-5 py-3"
          >
            <template v-slot:heading>
              <div class="text-center">
                <h1 class="display-2 font-weight-bold">
                  Administrador
                </h1>
              </div>
            </template>
            <div style="height: 70px;">
              <v-alert
                v-model="alert.active"
                :color="alert.color"
                style="white-space: pre-line;"
                dark
                dense
                transition="fade-transition"
              >
                {{ alert.text }}
              </v-alert>
            </div>

            <form @submit.prevent="login">
              <v-card-text class="text-center">
                <v-text-field
                  v-model="usuario.email"
                  v-model.trim="$v.usuario.email.$model"
                  color="secondary"
                  label="Email..."
                  prepend-icon="mdi-email"
                  :error="$v.usuario.email.$error"
                />

                <v-text-field
                  v-model="usuario.password"
                  v-model.trim="$v.usuario.password.$model"
                  class="mb-8"
                  color="secondary"
                  label="Contraseña..."
                  type="password"
                  prepend-icon="mdi-lock-outline"
                  :error="$v.usuario.password.$error"
                />
                <v-btn type="submit">
                  Ingresar
                </v-btn>
              </v-card-text>
            </form>
          </base-material-card>
        </v-slide-y-transition>
      </v-row>
    </v-container>
  </v-app>
</template>

<script>
  import Vue from 'vue'
  import { required, email } from 'vuelidate/lib/validators'
  import { mapActions } from 'vuex'

  export default {
    name: 'AdminLogin',
    data: () => {
      return {
        usuario: {
          email: '',
          password: '',
        },
        msg: false,
        alert: {
          active: false,
          text: '',
          color: '',
        },
      }
    },
    validations: {
      usuario: {
        email: {
          email,
        },
        password: {
          required,
        },
      },
    },
    methods: {
      ...mapActions(['guardarUsuario']),
      login () {
        const { email, password } = this.usuario

        const body = {
          email,
          password,
        }

        Vue.axios
          .post('/api/auth/login', body)
          .then(response => {
            const token = response.data.token
            this.guardarUsuario(token)
          // this.$router.push({ path: '/admin' })
          })
          .catch(e => {
            console.log(e.response.data)
            const res = e.response.data

            if (res.errors) {
              console.log('espress-validator')

              let salida = ''
              const errorsLength = res.errors.length
              res.errors.map((error, i) => {
                salida += error.msg
                if (errorsLength !== i + 1) {
                  salida += '\n'
                }
              })

              this.alertMsg(salida, 'warning', 2000)
            } else if (res.msg) {
              this.alertMsg(res.msg, 'warning', 2000)
            }
          })
      },
      alertMsg (text, color) {
        this.alert.active = true
        this.alert.text = text
        this.alert.color = color
        setTimeout(() => {
          this.alert.active = false
          this.alert.text = ''
          this.alert.color = ''
        }, 2000)
      },
    },
    metaInfo: {
      title: 'Ingreso Administración',
      titleTemplate: '%s - Centro de Terapia del Comportamiento',
      htmlAttrs: {
        lang: 'es',
        amp: true,
      },
    },
  }
</script>
